import React, { useEffect } from 'react'
import clsx from 'clsx'
import { makeStyles } from '@material-ui/styles'
import PropTypes from 'prop-types'
import {
  Card,
  CardContent,
  Grid,
  Typography,
  Icon,
  Tooltip,
  CircularProgress
} from '@material-ui/core'

import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward'
import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward'
import AttachMoneyIcon from '@material-ui/icons/AttachMoney'
import ErrorIcon from '@material-ui/icons/Error'

const useStyles = makeStyles((theme) => ({
  root: {
    height: '100%'
  },
  content: {
    alignItems: 'center',
    display: 'flex'
  },
  title: {
    fontWeight: 700
  },
  avatar: {
    backgroundColor: theme.palette.error.main,
    height: 56,
    width: 56
  },
  icon: {
    backgroundColor: theme.palette.icon.primary,
    height: 56,
    width: 56,
    borderRadius: '50%',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    color: theme.palette.white
  },
  difference: {
    marginTop: theme.spacing(2),
    display: 'flex',
    alignItems: 'center'
  },
  differenceIconSuccess: {
    color: theme.palette.success.main
  },
  differenceIconError: {
    color: theme.palette.error.main
  },
  differenceValueSuccess: {
    color: theme.palette.success.main,
    marginRight: theme.spacing(1)
  },
  differenceValueError: {
    color: theme.palette.error.main,
    marginRight: theme.spacing(1)
  },
  error: {
    color: theme.palette.error.main
  }
}))

const renderMetricTrend = (trend, classes) => {
  const { slope = 0, description = '' } = trend
  const text = (
    <React.Fragment>
      <Typography
        className={
          slope >= 0
            ? classes.differenceValueSuccess
            : classes.differenceValueError
        }
        variant='body2'
      >
        {trend.value || ''}
      </Typography>
      <Typography className={classes.caption} variant='caption'>
        {description || ''}
      </Typography>
    </React.Fragment>
  )
  return (
    <React.Fragment>
      {slope < 0 ? (
        <ArrowDownwardIcon className={classes.differenceIconError} />
      ) : (
        <ArrowUpwardIcon className={classes.differenceIconSuccess} />
      )}
      {text}
    </React.Fragment>
  )
}

const MetricCard = (props) => {
  console.log(props);
  const classes = useStyles()
  const {
    fetching,
    error,
    variant,
    spinnerColor,
    title,
    trend,
    fetchFunction
  } = props

  return (
    <Card className={clsx(classes.root)}>
      <CardContent>
        <Grid container justify='space-between'>
          <Grid item>
            <Typography className={classes.title} gutterBottom variant='body2'>
              {title}
            </Typography>
            <Typography variant='h3' className={classes.variant}>
              {fetching ? '' : error ? '' : variant}
            </Typography>
          </Grid>
          <Grid item>
            <Icon className={classes.icon}>
              {props.children || <AttachMoneyIcon />}
            </Icon>
          </Grid>
        </Grid>
        <div className={classes.difference}>
          {fetching ? (
            <CircularProgress color={spinnerColor || 'primary'} />
          ) : error ? (
            <React.Fragment>
              <Tooltip title={error}>
                <ErrorIcon color='error' />
              </Tooltip>
              <Typography variant='caption'>{error}</Typography>
            </React.Fragment>
          ) : (
            renderMetricTrend(trend, classes)
          )}
        </div>
      </CardContent>
    </Card>
  )
}

MetricCard.propTypes = {
  className: PropTypes.string
}

export default MetricCard
